<template>
  <div class="contact vh-100">
    <div class="container">
      <div class="row row-contact">
        <div
          class="
            col-lg-6
            d-flex
            align-items-center
            justify-content-center
            mx-auto
            flex-column
          "
        >
          <h4 class="mb-5 text-white">Contact me belowüòÉ</h4>
          <!-- Bootstrap 5 starter form -->
          <form
            id="contactForm"
            name="subscribe"
            @submit.prevent="sendGSpread"
            class="w-100 text-center"
          >
            <!-- Name input -->
            <div class="mb-3" data-aos="fade-up">
              <input
                class="form-control"
                id="name"
                type="text"
                placeholder="Name"
                name="nama"
                required
              />
            </div>

            <!-- Email address input -->
            <div class="mb-3" data-aos="fade-up" data-aos-delay="150">
              <input
                class="form-control"
                id="emailAddress"
                type="email"
                placeholder="Email Address"
                name="email"
                required
              />
            </div>

            <!-- Message input -->
            <div class="mb-3" data-aos="fade-up" data-aos-delay="250">
              <textarea
                class="form-control"
                id="message"
                type="text"
                placeholder="Message"
                style="height: 10rem"
                name="isi"
                required
              ></textarea>
            </div>

            <!-- Form submit button -->
            <div class="d-grid" data-aos="fade-up" data-aos-delay="350">
              <button
                class="btn btn-warning btn-lg"
                type="submit"
                v-if="submitSend"
              >
                Submit
              </button>
            </div>
            <div
              class="spinner-border text-warning mx-auto mt-5"
              role="status"
              v-if="submitLoad"
            >
              <span class="visually-hidden">Loading...</span>
            </div>
          </form>
          <div class="clicked-subscribe text-white mt-5" v-if="submitDone">
            <h2>Yeay..Thank you for contact me üëèüëè</h2>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      submitSend: true,
      submitLoad: false,
      submitDone: false,
    };
  },
  methods: {
    sendGSpread() {
      const form = document.forms["subscribe"];
      this.submitSend = false;
      this.submitLoad = true;
      this.$swal({
        title: "Are you sure?",
        showDenyButton: true,
        confirmButtonText: `Yes,send`,
        denyButtonText: `Cancel`,
      }).then((result) => {
        if (result.isConfirmed) {
          let scriptURL =
            "https://script.google.com/macros/s/AKfycbwP1wFVqv9BM7l-KpI81CdiE_H_CUTnri8MXBJHeqkc-Dcy4X-rXP4UQtSo_nQ3oDPf/exec";
          fetch(scriptURL, { method: "POST", body: new FormData(form) })
            .then((response) => {
              console.log("Success!", response);
              this.submitLoad = false;
              this.email = null;
              this.submitDone = true;
            })
            .catch((error) => console.error("Error!", error.message));

          this.$swal(
            "Yeay..I have received your messageüëç",
            "I will reply to your email soon"
          );
        } else if (result.isDenied) {
          this.$swal("Hmm..The message was not sentüòû", "", "error");
          this.submitSend = true;
          this.submitLoad = false;
        }
      });
    },
  },
  mounted() {
    document.title = "Mardha | Contact";
  },
};
</script>

<style scoped>
* {
  font-family: "Outfit", sans-serif;
}
.contact {
  background: linear-gradient(105.18deg, #6b73ff 50%, #ce70fb 100%);
}
.contact .row-contact {
  padding-top: 50px;
  height: 1000px;
}

@media screen and (max-width: 1500px) {
  .contact .row-contact {
    height: 800px;
  }
}

@media screen and (max-width: 768px) {
  .contact .row-contact {
    padding-top: 100px;
    height: 1000px;
  }
}

@media screen and (max-width: 500px) {
  .contact .row-contact {
    height: 700px;
  }
}
</style>
